.project-card {
  width: 33%;
  margin: 10px;
  display: block;
  float: left;
}

.project-image {
  height: 200px;
}

.project-header {
  display: block;
  overflow: hidden;
}

.project-title {
  float: left;
}

.project-date {
  float: right;
}

.project-content {
  clear: both;
}

.project-tag {
  margin-left: 6px;
}



